.share{
  width: 100%;
  height: 170%;
  border-radius: 10px;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0,0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201,201, 0.47);


  .shareWrapper{
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    .sharecontainer{
      position: relative;
      padding: 0 20px 10px 20px;
      

      .shareImg{
        width: 100%;
        object-fit: contain;

        
      }
      .closeIcon{
          position: absolute;
          top: 2px;
          right: 23px;
          background: #86828269;
          border-radius: 50%;
          padding: 2px;
          
  
        }
        .closeIcon:hover{
        background: red;
        cursor: pointer;
        border-radius: 50%;
        color: #fff;

      }
      
    }

    .shareTop{
      display: flex;
      align-items: center;

     .shareProfileImg{
        width: 50px;
        height: 50px;
        object-fit: cover;
        border-radius: 50%;
        margin-right: 10px;
      }

      .shareInput{
        border: none;
        background: transparent;
        font-size: 18px;
        width: 80%;
        outline: none;
      }
    }
    .shareHr{
      margin: 20px;
    }


    .shareBottom{
      display: flex;
      align-items: center;
      justify-content: space-between;

      .shareOptions{
        display: flex;
        align-items: center;
        margin-left: 20px;
        justify-content: space-around;
        width: 90%;

        .shareOption{
          display: flex;
          align-items: center;
          margin-right: 20px;
          cursor: pointer;

          .shareIcon{
            font-size: 30px;
            margin-right: 10px;
          }

          .shareOptionText{
            font-size: 14px;
            font-weight: 600;
          }
        }
      }
    }
   
  }
}