.menuLink{
  display: flex;
  padding: 10px;
  align-items: center;
  border-radius: 10px;
  font-weight: 500;
  cursor: pointer;

  .menuLinkText{
    margin-left: 10px;
    margin-right: 5px;
  }
  .menuLinkTextName{
    font-weight: 700;
    font-size: 18px;
  }

  &:hover{
    background: #011631;
    color: #fff;
  }
}
//Resiponsiveness
@media only screen and (max-width: 992px) {
  .menuLink{
    display: block;

    .menuLinkText{
      display: none;
    }
    .menuLinkTextName{
      display: none;
    }

    &:hover{
      display: block;
      background: #011631;
      color: #fff;
    }
  }
}