.editprofile{
    

  .editprofileWrapper{
      display: flex;

    .profileRight{
      flex: 12;

      .profileRightTop{

        .profileCover{
          height: 320px;
          position: relative;

          .profileCoverImg{
            width: 100%;
            height: 250px;
            object-fit: cover;
          }

          .profileUserImg{
            position: absolute;
            width: 150px;
            height: 150px;
            border-radius: 50%;
            object-fit: cover;
            cursor: pointer;
            top: 170px;
            left: 20%;
            
            
            border: 2px solid #666766;
          }
        }


        .profileInfo{
          display: flex;
          align-items: center;
          flex-direction: column;
          justify-content: center;
          

          .profileInfoName{
            font-size: 28px;
          }

          .profileInfoDesc{
            font-weight: 300;
          }
        }
      }
      .editprofileRightButton{
        display: flex;
        flex-direction: column;

        .top{
          -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
          box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.983);
          margin: 20px;
          padding: 10px;
          display: flex;

          .h1{
            color: #666;
            font-size: 20px;
          }
        }
        .bottom{
          -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
          box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.983);
          margin: 20px;
          padding: 10px;
          display: flex;
          .left{
            flex: 1;
            img{
              width: 150px;
              height: 150px;
              margin: 40px 0 40px 40px;
              border-radius: 50%;
            }
          }

          .right{
            flex: 2;

            form{
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              
            }

            .formInput{
              width: 100%;
              margin-top: 10px;

              label{
                display: flex;
                align-items: center;
                gap: 5px;

                .Icon{
                cursor: pointer;
                
              }
              }

              
              
              input{
                width: 100%;
                font-size: 18px;
                padding: 8px;
                border: none;
                outline: none;
                border-bottom: 3px solid gray;
                border-radius: 8px;
                background: transparent;
                color:rgba(156, 156, 156, 0.837);
              }
            }
          }

          .updateButton{
            width: 100px;
            padding: 8px 5px;
            font-size: 12px;
            margin-top: 20px;
            border: none;
            background: rgb(26, 26, 163);
            color: #fff;
            border-radius: 4px;
            cursor: pointer;
          }

        }
      }
    }
  }
}
