.online{


  .rightbarFrend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    cursor: pointer;

    .rightbarProfileImgContainer{
      margin-right: 10px;
      position: relative;


      .rightbarProfileImg{
        width: 40px;
        height: 40px;
        border-radius: 50%;
        object-fit: cover;
      }
      .rightbarOnline{
        position: absolute;
        content: '';
        height: 10px;
        width: 10px;
        background: #0ac10d;
        top: 0;
        right: 0;
        border-radius: 50%;
        border: 1px solid #fff;
      }

    }
    .rightbarUsername{
      font-weight: 500;
      color: #333;
    }
  }
}
//Responsiveness;
@media only screen and (max-width: 768px) {
  .rightbar{
    display: none;
  }
}