.post{
  width: 100%;
  margin: 30px 0;
  border-radius: 10px;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0,0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201,201, 0.47);

  .postWrapper{
    padding: 20px;

    .postTop{
      display: flex;
      align-items: center;
      justify-content: space-between;

      .postTopLeft{
        display: flex;
        align-items: center;

        .postProfileImg{
          width: 40px;
          height: 40px;
          border-radius: 50%;
          object-fit: cover;
          cursor: pointer;
        }
        .postUsername{
          font-size: 15px;
          font-weight: 500;
          margin: 0 10px;
          cursor: pointer;
        }
        .postDate{
          font-size: 14px;
        }


      }
      
        .postTopRight{


          .postVertButton{
            font-size: 30px;
            color: #333;
            cursor: pointer;
          }
        }
    }

    .postCenter{
      margin: 20px 0;

      .postText{

      }

      .postImg{
        width: 100%;
        cursor: pointer;
        object-fit: contain;
        border-radius: 10px;
        margin-top: 20px;
      }
    }

    .postBottom{
      display: flex;
      align-items: center;
      justify-content: space-between;

      .postBottomLeft{
        display: flex;
        align-items: center;
        gap: 10px;
        .bottomLeftIcon{
          font-size: 30px;
          cursor: pointer;
        }
        .postLikeCounter{
          font-size: 15px;
        }
      }
    }
    .postBottomRight{


      .postCommentText{
        font-size: 15px;
        cursor: pointer;
      }
    }

    .footerHr{
      margin: 10px 0;
    }

    .postBottomFooter{
      display: flex;
      align-items: center;
      justify-content: space-between;


      .postBottomFooterItem{
        display: flex;
        align-items: center;
        padding: 7px;
        cursor: pointer;

        .footerIcon{
          color: #333;
          font-size: 30px;
          margin-right: 10px;

        }

        .footerText{
          font-weight: 500;
        }

      }
      .postBottomFooterItem:hover{
      background: rgba(0, 0,0, 0.3);
      transition: 0.3s;
      border-radius: 10px;
      color: #fff;
    }
    }
    
  }
}