.profile{


  .profileWrapper{
    display: flex;

    .profileRight{
      flex: 12;

      .profileRightTop{

        .profileCover{
          height: 320px;
          position: relative;

          .profileCoverImg{
            width: 100%;
            height: 250px;
            object-fit: cover;
          }

          .profileUserImg{
            position: absolute;
            width: 150px;
            height: 150px;
            border-radius: 50%;
            object-fit: cover;
            cursor: pointer;
            top: 170px;
            left: 20%;
            
            
            border: 2px solid #666766;
          }
        }


        .profileInfo{
          display: flex;
          align-items: center;
          flex-direction: column;
          justify-content: center;
          

          .profileInfoName{
            font-size: 28px;
          }

          .profileInfoDesc{
            font-weight: 300;
          }
        }
      }
      .profileRightBottom{
        display: flex;
      }
    }
  }
}
@media only screen and (max-width: 992px ) {
  .profile{
  .profileWrapper{
    .profileRight{

      .profileRightTop{

        .profileCover{
          height: 320px;
          position: relative;

          .profileCoverImg{
            width: 100%;
            height: 250px;
            object-fit: cover;
          }

          .profileUserImg{
            position: absolute;
            width: 150px;
            height: 150px;
            border-radius: 50%;
            object-fit: cover;
            cursor: pointer;
            right: 0;
            left: 0;
            margin: auto;
            
            
            
            border: 2px solid #666766;
          }
        }
      }
      
    }
  }
}
}
