.sidebar{
  flex: 3;
  height: calc(100vh - 50px);
  position: sticky;
  top: 50px;
  overflow-y: scroll;

  .sidebarWrapper{
    padding: 20px;

    .sidebarButton{
        background: #011631;
        color: #fff;
        border-radius: 5px;
        font-weight: 500;
        padding: 10px;
        margin: 20px;
        width: 150px;
        border: none;
        cursor: pointer;
      }
    
      .sidebarHr{
        margin: 10px 0;
      }


      .sidebarFriendList{
        padding: 0;
        margin: 0;
        list-style: none;
        
        
      }
  }

  
}
::-webkit-scrollbar{
  width: 5px;
}
::-webkit-scrollbar-track{
  background: #fff;
}
::-webkit-scrollbar-thumb{
  background: gray;
}
//Responsiveness

@media only screen and (max-width: 992px){
  .sidebar{
    flex: 1;
    .sidebarWrapper{
      padding: 17px;

      .sidebarButton{
        display: none;
      }
      .sidebarHr{
        display: none;
      }
    }
  }
      ::-webkit-scrollbar{
        display: none;
      }
      ::-webkit-scrollbar-track{
        display: none;
      }
      ::-webkit-scrollbar-thumb{
        display: none;
      }
}
@media only screen and (max-width: 768px){
  .sidebar{
    display: block;
    .sidebarWrapper{
      padding: 3px;

    }
  }
}
@media only screen and (max-width: 576px){
  .sidebar{
    display: none;
    
  }
}