.sidebarFriend{
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  cursor: pointer;


  .sidebarFriendImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
  }

  .sidebarFrindName{
    font-weight: 500;
    color: #000;
  }
}
//Responsiveness

@media only screen and (max-width: 992px) {
  .sidebarFrindName{
    display: none;
  }
}

