.login{
  background: rgba(83, 82, 82, 0.29);
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .loginWrapper{
    width: 80%;
  display: flex;

  .registerLeft{
    display: flex;
    flex-direction: column;
    flex: 1;
    margin: 0 20px ;
    justify-content: center;

    .LogoLogin{
      font-size: 50px;
      font-weight: 800;
      color: blueviolet;
      margin-bottom: 10px;
    }

    .registerDesc{
      font-size: 28px;
      display: flex;
    }
  }


  .registerRight{
    flex: 1;
    flex-direction: column;
    margin: 0 20px;
    justify-content: center;


    .registerBox{
      display: flex;
      flex-direction: column;
      background: #fff;
      border-radius: 10px;
      width: 100%;

        

        .loginFormBox{

          padding: 30px;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          border-radius: 10px;
          background: blueviolet;

          .registerInput{
            height: 50px;
            border-radius: 10px;
            border: 0;
            outline: none;
            padding-left: 20px;
            font-size: 18px;


            
          }

          
          .loginRegisterButton{
              height: 50px;
              width: 100%;
              background: rgb(28, 155, 2);
              color: #fff;
              font-size: 20px;
              border: 0;
              border-radius: 10px;
              margin-bottom: 5px;
              cursor: pointer;
            }
            .loginSignUpButton{
              height: 50px;
              width: 100%;
              background: blue;
              color: #fff;
              font-size: 20px;
              border: 0;
              border-radius: 10px;
              margin-bottom: 5px;
              cursor: pointer;

            }
          
        }
      
    }
  }

  }

}