.profileRightBar{
  flex: 3.5;
  padding: 0 20px 0 0;
  

  .ProfileRightBarHeding{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0;

    .profileRightBarTitle{
      font-size: 18px;
      font-weight: 600;
    }
    .editButton{
      padding: 7px;
      border: 0;
      border-radius: 4px;
      background: rgb(24, 24, 160);
      color: #fff;
      cursor: pointer;
    }

  }

  .profileRightBarInfo{
    margin-bottom: 30px;

    .profileRightBarInfoItem{
      margin-bottom: 15px;

      .profileRightBarInfoKey{
        font-weight: 600;
        margin-right: 20px;
      }

      .profileRightBarInfoValue{
        font-weight: 500;
      }
    }
  }

  .profileRightBarTitle{

  }

  .profileRightBarFollowings{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 20px;
    

    .profileRightBarFollowing{
      display: flex;
      flex-direction: column;
      margin-bottom: 20px;
      cursor: pointer;

      .profileRightBarFollowingImg{
        width: 100px;
        height: 100px;
        object-fit: cover;
        border-radius: 5px;
      }

      .profileRightBarFollowingName{
        align-self: center;
        margin-top: 5px;
      }
    }
  }

}
@media only screen and (max-width: 768px) {
  .profileRightBar{
    display: none;
  }
}