.register{
  background: rgba(83, 82, 82, 0.29);
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .registerWrapper{
    width: 80%;
    display: flex;

    .registerLeft{
      display: flex;
      flex-direction: column;
      flex: 1;
      margin: 0 20px ;
      justify-content: center;

      .registerLogo{
        font-size: 70px;
        font-weight: 800;
        color: blueviolet;
        margin-bottom: 10px;
      }

      .registerDesc{
        font-size: 28px;
        display: flex;
      }
    }

    .registerRight{
      flex: 1;
      flex-direction: column;
      margin: 0 20px;
      justify-content: center;


      .registerBox{
        display: flex;
        flex-direction: column;
        background: #fff;
        border-radius: 10px;
        width: 100%;

        .top{
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin: 30px;

          .profileImg{
            display: flex;
            width: 70px;
            height: 70px;
            border-radius: 50%;
            object-fit: cover;
          }
          .formInput{
            display: flex;
            align-items: center;

            label{
              display: flex;
              align-items: center;
            }

            .icon{
              cursor: pointer;
              font-size: 34px;
              color: rgba(137, 43, 226, 0.628);
              margin-left: 10px;
            }
          }

        }

        .bottom{
          

          .bottomFormBox{
            height: 400px;
            padding: 5px 40px 10px 40px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            border-radius: 10px;
            background: blueviolet;

            .registerInput{
              height: 50px;
              border-radius: 10px;
              border: 0;
              outline: none;
              padding-left: 20px;
              font-size: 18px;


              
            }

            .displayFlex{
              display: flex;
              align-items: center;
              justify-content: center;

             .registerButton{
                height: 35px;
                width: 50%;
                background: blue;
                color: #fff;
                font-size: 20px;
                border: 0;
                border-radius: 10px;
                margin-bottom: 5px;
                padding: 0 5px 0 5px;
                cursor: pointer;
              }
              .logintoaccountbutton{
                height: 35px;
                width: 100%;
                background: rgb(28, 155, 2);
                color: #fff;
                font-size: 20px;
                border: 0;
                border-radius: 10px;
                margin-bottom: 5px;
                cursor: pointer;
                margin-left: 20px;
                padding: 0 5px 0 5px;
                
              }
            }
          }
        }
      }
    }
  }
}