.stories{
  display: flex;
  margin-bottom: 30px;
  margin: 5px;
  justify-content: space-between;
  border-radius: 10px;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  .storyCard{
    position: relative;
    flex-basis: 20%;
    margin: 5px;
    display: flex;

    .overlay{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      background: #000000a1;
      opacity: 0.9;
    }
    .storyProfile{
      display: flex;
      position: absolute;
      width: 45px;
      height: 45px;
      object-fit: cover;
      border-radius: 50%;
      top: 10px;
      left: 10px;
      border: 3px solid #0064e8;
    }
    .storybackground{
      width: 100%;
      height: 220px;
      border-radius: 10px;
      object-fit: cover;
      cursor: pointer;
    }
    .storyadd{
      position: absolute;
      left: 50%;
      width: 43px;
      bottom: 50px;
      transform: translateX(-50%);
      color: #0047a3;
      cursor: pointer;
    }
    .text{
      position: absolute;
      width: 100%;
      font-size: 14px;
      font-weight: 500;
      bottom: 10px;
      text-align: center;
      cursor: pointer;
      color: #fff;
    }
  }
}